<script>
// @ is an alias to /src
// import HelloWorld from '@/components/HelloWorld.vue'
import axios from 'axios';

export default {
  name: 'Home',
  data() {
    return {
      btc: '',
      eth: '',
      ltc: '',
      btcX: 0,
      ethX: 0,
      ltcX: 0,
    }
  },
  created() {
    this.getPrices();
    setInterval(() => {
      this.getPrices();
    }, 10000);
  },
  methods: {
    toFixed(num) {
     return num.toString().match(/^-?\d+(?:\.\d{0,2})?/)[0];
    },
    getPrices() {
      axios.get('https://api.coingecko.com/api/v3/exchange_rates').then((response) => {
        if (this.toFixed(response.data.rates.usd.value) > this.btc) {
          this.btcX = 1;
        } else if (this.toFixed(response.data.rates.usd.value) == this.btc) {
          this.btcX = 0;
        } else {
          this.btcX = -1;
        }
        this.btc = this.toFixed(response.data.rates.usd.value);
        if (this.toFixed(response.data.rates.usd.value/response.data.rates.eth.value) > this.eth) {
          this.ethX = 1;
        } else if (this.toFixed(response.data.rates.usd.value/response.data.rates.eth.value) == this.eth) {
          this.ethX = 0;
        } else {
          this.ethX = -1;
        }
        this.eth = this.toFixed(response.data.rates.usd.value/response.data.rates.eth.value);
        if (this.toFixed(response.data.rates.usd.value/response.data.rates.ltc.value) > this.ltc) {
          this.ltcX = 1;
        } else if (this.toFixed(response.data.rates.usd.value/response.data.rates.ltc.value) == this.ltc) {
          this.ltcX = 0;
        } else {
          this.ltcX = -1;
        }
        this.ltc = this.toFixed(response.data.rates.usd.value/response.data.rates.ltc.value);
      });
    }
  }
}
</script>
